			},
			// 话题
			topic: {
				swiper: [],
				nav: [],
				list: []
			}
		}
	},
	onLoad () {
		uni.getSystemInfo ({
			success: (res) => {
				let height= res.windowHeight - uni.upx2px(100);
				this.swiperHeight = height;
			}
		});
		
		this.__init();
		
	},
	methods: {
		// 点击搜索
		openSearch () {
			uni.navigateTo({
				url: '../search/search?searchType=topic'
			})
		},
		// 初始化数据
		__init () {
			this.getSwiper();
			this.getNav();
			this.getHot();
		},
		// 获取广告
		async getSwiper () {
			let [err, res] = await this.$http.get('/adsense/0');
			if (!this.$http.errorCheck(err, res)) return;
			let arr = [];
			let list = res.data.data.list;
			for (let i = 0; i < list.length; i++) {
				arr.push({
					src: list[i].src,
					url: list[i].url,
					id: list[i].id,
					type: list[i].type
				});
			}
			this.topic.swiper = arr;
		},
		// 获取热门分类
		async getNav(){
			let [err, res] = await this.$http.get('/topicclass');
			if (!this.$http.errorCheck(err, res)) return;
			let arr = [];
			let list = res.data.data.list;
			for (let i = 0; i < list.length; i++) {
				arr.push({
					id: list[i].id,
					name: list[i].classname
				});
			}
			this.topic.nav = arr;
		},
		// 获取热门话题
		async getHot(){
			let [err, res] = await this.$http.get('/hottopic');
			if (!this.$http.errorCheck(err, res)) return;
			let arr = [];
			let list = res.data.data.list;
			for (let i = 0; i < list.length; i++) {
				arr.push({
					id: list[i].id,
					title: list[i].title,
					titlepic: list[i].titlepic,
					type: list[i].type,
					totalnum: list[i].post_count,
					todaynum: list[i].todaypost_count
				});
			}
			this.topic.list = arr;
		},
		// 导航栏内容切换
		changeTab (index) {
			this.tabIndex = index;
		},
		// 关注
		guanzhu (index) {
			this.dataList.list[index].isguanzhu = true;
			uni.showToast({
				title: '关注成功！'
			});
		},
		// 上拉加载
		loadmore (index) {
			if (this.dataList.loadtext != "上拉加载") {return ;}
			this.dataList.loadtext = "加载中。。。";
			
			setTimeout(() => {
				let data = {
					userpic: '../../static/demo/datapic/1.jpg', // 头像
					username: '忆忘', // 昵称
					sex: 1, // 0 男 1 女
					age: 25, // 年龄
					isguanzhu: false, // 是否关注
					title: '黑夜的烟火,绚丽而短暂', // 标题
					titlepic: '../../static/demo/datapic/14.jpg', // 主题 图片
					video: false, // 主题 视屏
					share: false, // 主题 分享
					path: "深圳 龙岗", // 地址
					sharenum: 20, // 分享数量
					commentnum: 30, // 评论数
					goodnum: 20 // 点赞
					
				}
				this.dataList.list.push(data);
				this.dataList.loadtext = "上拉加载";
			}, 1000);
			// this.dataList.loadtext = "没有更多数据了";
		},
		// 滑动事件
		tabChange (e) {
			this.tabIndex = e.detail.current;
		}
	}
}
